<template>
  <div class="MaterialAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12">
                <v-text-field
                  prepend-icon="mdi-codepen"
                  label=" - "
                  v-model="list.name"
                  :rules="emptyRules"
                  v-if="dialog"
                  autofocus
                ></v-text-field>
              </v-col>
              
              <v-col cols="12" v-if="processType == 'edit'">
                <v-text-field
                  prepend-icon="mdi-locker"
                  label=""
                  v-model="list.store"
                ></v-text-field>
              </v-col>
              
              <v-col cols="12">
                <v-text-field
                  prepend-icon="mdi-note"
                  label=""
                  v-model="list.note"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            ></v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            ></v-btn
          >
        </v-card-actions>
        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || ""],
      branchs:[],
      units:['',''],
    };
  },

  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //,
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {};
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //,
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //
    },
    addOK() {
      //
      if (this.$refs.form.validate()) {
        this.list.personnel_snkey = this.$store.state.pData.snkey;
        this.list.create_man = this.$store.state.pData.name + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //,,
        this.list.store = 0;

        let postData = {
          personnel_snkey: this.list.personnel_snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        let url = "general/add/" + this.$store.state.databaseName + "/material";
        this.myAxios(url,postData)          
          .then((rs) => {
            console.log(rs);
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "" });
              this.$store.dispatch("getItems",'material');
            }
          }).catch((err) => {
            this.$swal({ icon: "error", text: "!!:" + err });
          });
      }
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //,,
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        let url ="general/edit/" + this.$store.state.databaseName + "/material";
        this.myAxios(url,postData)
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "", });
              this.$store.dispatch("getItems",'material');
            }
          }).catch((err) => {
            this.$swal({ icon: "error", text: "!!:" + err });
          });
      }
    },
    
  },
};
</script>
