<template>
  <div class="materialAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="3">
                <v-text-field
                  type="date"
                  prepend-icon="mdi-calendar"
                  label=""
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>

              <v-col cols="12" md="5">
                <v-autocomplete
                  :items="thirdpartyItems"
                  prepend-icon="mdi-handshake"
                  label=""
                  v-model="list.thirdparty"
                  :rules="emptyRules"
                  v-if="dialog"
                  autofocus
                  return-object
                >
                </v-autocomplete>
              </v-col>
              <v-col cols="12" md="4">
                <v-combobox
                  :items="presonnelItems"
                  prepend-icon="mdi-human-greeting"
                  label=""
                  v-model="list.takeman"
                  :rules="emptyRules"
                >
                </v-combobox>
              </v-col>
              <v-col cols="12">
                <v-combobox
                  prepend-icon="mdi-codepen"
                  label=" - "
                  :items="materialItems"
                  v-model="list.material"
                  :rules="emptyRules"
                  ref="materialname"
                >
                </v-combobox>
              </v-col>
              
              <v-col cols="12" md="4">
                <v-text-field
                  type="number"
                  prepend-icon="mdi-cash"
                  label=""
                  v-model="list.price"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field
                  type="number"
                  prepend-icon="mdi-view-sequential"
                  label=""
                  v-model="list.quantity"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="4">
                <v-text-field
                  prepend-icon="mdi-weight-kilogram"
                  label=""
                  v-model="list.unit"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-text-field 
                prepend-icon="mdi-note"
                label="" v-model="list.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            ></v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            ></v-btn
          >
        </v-card-actions>

        <v-card class="mx-2 grey lighten-4" v-if="tempDatas.length">
          <v-card-title>
            
            <v-spacer></v-spacer>
            : {{ quantityTotal }} : {{ importTotal }}
          </v-card-title>
          <v-card-text>
            <v-simple-table fixed-header class="text-no-wrap" max-height="65vh">
              <template v-slot:default>
                <thead>
                  <tr class="green lighten-1 white--text font-weight-bold">
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>- </td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) of tempDatas" :key="index">
                    <td>
                      <v-icon class="error" @click.stop="delTempData(index)"
                        >mdi-close</v-icon
                      >
                      {{ index + 1 }}.
                    </td>
                    <td>{{ item.date }}</td>
                    <td>{{ item.takeman.text }}</td>
                    <td>{{ item.material.name }}</td>
                    <td>{{ item.price }}</td>
                    <td>{{ item.quantity }}</td>
                    <td>{{ item.price * item.quantity }}</td>
                    <td>{{ item.unit }}</td>
                    <td>{{ item.note }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="error" @click="finishOK"></v-btn>
          </v-card-actions>
        </v-card>

        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //
      oldList: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || ""],
      tempDatas: [], //
      presonnelItems: [],
    };
  },

  computed: {
    thirdpartyItems() {
      return this.$store.state.thirdpartyItems.map((i) => {
        return {
          text: `${i.unit} / ${i.name}`,
          snkey: i.snkey,
        };
      });
    },
    materialItems() {
      return this.$store.state.materialItems.map((i) => {
        return {
          text: `${i.name}`,
          name: i.name,
          snkey: i.snkey,
        };
      });
    },
    importTotal() {
      return this.tempDatas.reduce((total, item) => {
        return (total += item.price * item.quantity);
      }, 0);
    },
    quantityTotal() {
      return this.tempDatas.reduce((total, item) => {
        return (total += parseInt(item.quantity));
      }, 0);
    },
  },

  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.getItems('personnel');
    this.$store.dispatch("getItems","thirdparty");
  },

  methods: {
    addProcess() {
      //,
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        price: 0,
        quantity: 0,
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    //,
    editProcess(item) {
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item;
      this.list = { ...item }; //
      this.tempDatas = [];
    },
    // -> 
    addOK() {
      if (this.$refs.form.validate()) {
        //
        if (typeof(this.list.takeman) != "object" || typeof(this.list.material) != "object") {
          this.$swal({
            icon: "warning",
            text: "!!",
          });
          return false;
        }
        //end

        this.list.personnel_snkey = this.$store.state.pData.snkey;
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //,,

        this.tempDatas.push({ ...this.list });
        this.list.price = 0;
        this.list.quantity = 1;
        this.$refs.materialname.focus();
        this.$refs.form.resetValidation();
      }
    },
    delTempData(index) {
      this.tempDatas.splice(index, 1);
    },
    //
    finishOK() {
      this.dialog = false;

      var runRS = new Promise((resolve, reject) => {
        this.tempDatas
          .forEach((tempItem) => {
            let postData = {
              personnel_snkey: tempItem.personnel_snkey,
              datalist: JSON.stringify(tempItem),
            };

            this.myAxios(
              "general/add/" +
                this.$store.state.databaseName +
                "/materialimport",
              postData
            )
              .then((rs) => {
                if (rs.state == 1) {
                  this.$swal({ icon: "success", text: "" });
                  // this.$emit("getAllData");
                  resolve(rs);
                }
              })
              .catch((err) => {
                this.$swal({ icon: "err", text: "!!:" + err });
                reject(err);
              });

            //
            let matchData = this.$store.state.materialItems.find((i) => {
              return i.snkey == tempItem.material.snkey;
            });
            if(!matchData.store){matchData.store = 0}
            matchData.store = parseInt(matchData.store) + parseInt(tempItem.quantity);
            this.updateStore("material", matchData); //('',)
            //end
          });
      });

      //
      runRS.then(() => {
        this.$emit("getAllData");
        this.tempDatas = [];
      });

    },
    
    editOK() {
      //
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //,,
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        let url =
          "general/edit/" + this.$store.state.databaseName + "/materialimport";
        this.myAxios(url, postData)
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "" });
              this.$emit('editFinish',this.list)
            }
          })
          .catch((err) => {
            this.$swal({ icon: "error", text: "!!:" + err });
          });

        //
        let matchData = this.$store.state.materialItems.find((i) => {
          return i.snkey == this.list.material.snkey;
        });
        matchData.store = parseInt(matchData.store) + (parseInt(this.list.quantity) - parseInt(this.oldList.quantity));
        this.updateStore("material", matchData); //('',)
        //end
      }
    },
    getItems(database) {
      let url =
        "general/getAll/" + this.$store.state.databaseName + "/" + database;

      this.myAxios(url).then((rs) => {
        this.presonnelItems = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          return {
            snkey : newItem.snkey,
            text : newItem.name,
          };
        });
      });
    },
  },
};
</script>
